import React, { useLayoutEffect, useState } from "react";
import jsQR from "jsqr";
import axios from "axios";

const QrCodeScanner = () => {
  const [players, setPlayers] = useState([]);
  const [currentPlayer, setCurrentPlayer] = useState({});
  useLayoutEffect(() => {
    var video = document.createElement("video");
    var canvasElement = document.getElementById("canvas");
    var canvas = canvasElement.getContext("2d");
    var loadingMessage = document.getElementById("loadingMessage");
    var outputContainer = document.getElementById("output");
    var outputMessage = document.getElementById("outputMessage");
    var outputData = document.getElementById("outputData");

    function drawLine(begin, end, color) {
      canvas.beginPath();
      canvas.moveTo(begin.x, begin.y);
      canvas.lineTo(end.x, end.y);
      canvas.lineWidth = 4;
      canvas.strokeStyle = color;
      canvas.stroke();
    }

    // Use facingMode: environment to attemt to get the front camera on phones
    navigator.mediaDevices
      .getUserMedia({ video: { facingMode: "environment" } })
      .then(function (stream) {
        video.srcObject = stream;
        video.setAttribute("playsinline", true); // required to tell iOS safari we don't want fullscreen
        video.play();
        requestAnimationFrame(tick);
      });

    async function tick() {
      loadingMessage.innerText = "⌛ Loading video...";
      if (video.readyState === video.HAVE_ENOUGH_DATA) {
        loadingMessage.hidden = true;
        canvasElement.hidden = false;
        outputContainer.hidden = false;

        canvasElement.height = video.videoHeight;
        canvasElement.width = video.videoWidth;
        canvas.drawImage(
          video,
          0,
          0,
          canvasElement.width,
          canvasElement.height
        );
        var imageData = canvas.getImageData(
          0,
          0,
          canvasElement.width,
          canvasElement.height
        );
        var code = jsQR(imageData.data, imageData.width, imageData.height, {
          inversionAttempts: "dontInvert",
        });
        if (code) {
          drawLine(
            code.location.topLeftCorner,
            code.location.topRightCorner,
            "#FF3B58"
          );
          drawLine(
            code.location.topRightCorner,
            code.location.bottomRightCorner,
            "#FF3B58"
          );
          drawLine(
            code.location.bottomRightCorner,
            code.location.bottomLeftCorner,
            "#FF3B58"
          );
          drawLine(
            code.location.bottomLeftCorner,
            code.location.topLeftCorner,
            "#FF3B58"
          );
          outputMessage.hidden = true;
          outputData.parentElement.hidden = false;
          outputData.innerText = code.data;
          const res = await axios.post(
            "https://qrcode-scanner-ap.herokuapp.com/api/scanCardData",
            { cardData: code.data, currentPlayer }
          );
          console.log("code =>", res.data);
          if (res.data.msg) {
            alert("Scan complete");
          }
          setPlayers(res.data.players);
          setCurrentPlayer(res.data.currentPlayer);
        } else {
          outputMessage.hidden = false;
          outputData.parentElement.hidden = true;
        }
      }
      requestAnimationFrame(tick);
    }
  }, [currentPlayer]);

  return (
    <div>
      <h1>jsQR Demo</h1>
      {/*<a id="githubLink" href="https://github.com/cozmo/jsQR">
        View documentation on Github
      </a>*/}
      <p>Pure JavaScript QR code decoding library.</p>
      <div id="loadingMessage">
        🎥 Unable to access video stream (please make sure you have a webcam
        enabled)
      </div>
      <canvas id="canvas" hidden></canvas>
      <div id="output" hidden>
        <div id="outputMessage">No QR code detected.</div>
        <div hidden>
          <b>Data:</b> <span id="outputData"></span>
        </div>
      </div>
      <div>
        <div>Current Player- {currentPlayer.name}</div>
        <table align="center" border="2">
          <thead>
            <tr>
              <th>Player</th>
              <th>Card1</th>
              <th>Card2</th>
            </tr>
          </thead>
          <tbody>
            {players.map((player) => (
              <tr>
                <td> {player.name}</td>

                {player.cards.map((card) => (
                  <td className="cards">
                    <img
                      src={require(`./assets/cards/${card}.svg`).default}
                      alt={card}
                    />
                  </td>
                ))}
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default QrCodeScanner;
